import React from 'react';
import "./bottomPage.css";
export default function BottomPage (props) {

let style;
if ( props.data.isMain) {
    style={
        height:45+"vh",
        display:"none"
    }
}
else {
    style={
        height:45+"vh",
        display:"flex"
    }
}

    return (

        <div className="bottom-section" style={style}>
            <div className="bottom-container">
                <div className="grid-element ip-box"> 
                <p>Your IP address:</p>
               <p> {props.data.ip}</p> 
               
               </div>
                <div className="grid-element time-zone">
                 <p> Your time zone:</p>
                    <p> {props.data.timeZone}</p>
                </div>
                <div className="grid-element weather-conditions">
                
                    <p>Weather in {props.data.city}:</p>
                    <p>  {props.data.temp} {'\u00b0'}C, 
                    and it feels like {props.data.feelsLike} {'\u00b0'}C </p>
                </div>
            </div>
        </div>
    )
}