import React, {Component } from 'react';
import "./mainPage.css";
import BottomPage from "./BottomPage";

class MainPage extends Component {

    constructor () {
        super();
        this.state={
            isMain:true,
            background:"",
            city:false,
            location:"",
            time:"",
            temp:"",
            feelsLike:"",
            ip:"",
            timeZone:"",
            isLoaded:false
        }
        this.handleSeeMore=this.handleSeeMore.bind(this)
    }
    handleSeeMore() {
        
        this.setState({
            isMain: !this.state.isMain
        })
     
    }

    componentDidMount() {
        fetch ("https://freegeoip.app/json/")
            .then(res=>res.json())
                .then(res=>{
                    const data=res;
                    this.setState({
                        location:data.region_name + " " + data.country_name,
                        city:data.region_name,
                        ip:data.ip,
                        timeZone:data.time_zone
                    })
                    fetch ("http://api.openweathermap.org/data/2.5/weather?q="+this.state.city+"&units=metric&appid=111f6d9f89b43a9c83638b626261c592")
                    .then (res=>res.json())
                         .then (res=>{
                             this.setState({
                                 temp:res.main.temp,
                                 feelsLike:res.main.feels_like
                             })
    
                        })
                })
             
               
                    
                fetch ("https://worldtimeapi.org/api/ip")
                .then (res=>res.json())
                    .then (res=>{
                        const data=res;
                        this.setState( {
                            time: this.getTime(data.datetime)
                        } )
                    })
                   

                setInterval(()=>{
                    fetch ("https://worldtimeapi.org/api/ip")
                    .then (res=>res.json())
                        .then (res=>{
                            const data=res;
                            this.setState( {
                                time: this.getTime(data.datetime)
                            } )
                        })
                },45000)
                
    }
 

    getTime(date) {
        let time=new Date(date);
        time = time.getHours() + ":" + (time.getMinutes()<10 ? "0"+ time.getMinutes() : time.getMinutes());
        return time;
        }
       

    render(){
        return (
            <div> 
<div className="main-page" style={this.state.isMain ? {height:100+"vh"} : {height:55+"vh"}}>
            <div className="page-container">
                <div className="left-container">
                <p className="greeting">It's currently,</p>
                <h2 className="time">{this.state.time ? this.state.time:""}</h2>
                <p>{this.state.location}</p>
                </div>

                <div className="right-container">
                    <div>

                    <p>Weather in {this.state.location}</p>
                    <h2>{this.state.temp ? this.state.temp : ""} {'\u00b0'}C </h2><span></span>
                    </div>

                    <div className="button" onClick={this.handleSeeMore}> {this.state.isMain ? "See more" : "See less"}</div>
                </div>

            </div>
</div>
      <BottomPage data={this.state}/>
   

</div>
        )
    }
}

export default MainPage;